.App {
    width: 700px;
    margin-top: 150px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    background-color: white;
    border-radius: 10px;
    align-items: center;
    flex-direction: column;
}

.header {
    width: 80%;
    font-size: 28px;
}

@media only screen and (max-width: 375px) {
    .App {
        width: 350px;
    }
}
